// Navbar
#navbar {
    background-color: #75635b;
    border: 0px;
    
    .container {
        padding: 0;
        position: relative;
        width: 100%;
    }

    .navbar-header {
        position: absolute;
        width: 100%;
        height: 0;
    }

    #navbar-collapse .navbar-nav {
        background-color: #75635b;

        li.dropdown:hover {
            background-color: #e7e7e7;
            border-radius: 4px 4px 0 0;
            color: #555;

            a.dropdown-toggle {
                border-radius: 4px 4px 0 0;
                color: #555;
            }

            > ul.dropdown-menu {
                background-color: #e7e7e7;
                border: 0;
                border-radius: 0 4px 4px 4px;
                
                > li > a {
                    color: #555;
                    margin: 0 5px;
                    padding: 5px 15px 5px 15px;

                    &:hover,
                    &.active-trail {
                        background-color: #bfbfbf;
                        border-radius: 4px;
                    }
                }

                > li.dropdown > ul.dropdown-menu {
                    background-color: lighten(#e7e7e7, 5%);
                }
            }

            @include breakpoint(0px 979px) {
                border-radius: 4px;
                display: flex;
                flex-flow: column;

                a.dropdown-toggle {
                    border-radius: 4px 4px 0 0;
                }

                > ul.dropdown-menu {
                    position: static;
                    border-radius: 0 0 4px 4px;
                }
            }
        }
        
        @include breakpoint(980px) {
            float: right;
            min-height: 50px;
            display: flex;
            align-items: center;
        }

        > li {
            margin: 3px;
            > a {
                color: #FFF;
                display: inline-block;
                white-space: nowrap;
                border-radius: 4px;
                padding: 5px;
                @include fluid-type(320px, 1250px, 14px, 18px); // values inside are ($min_width,$max_width, $min_font, $max_font);
                
                &:hover, &.active-trail {
                    color: #555;
                    background-color: #e7e7e7;
                }
            }

            &.open {
                > a {
                    color: #555;
                    border-radius: 4px 4px 0 0;
                    background-color: #e7e7e7;
                }
                .dropdown-menu {
                    background-color: #e7e7e7;
                    border: 0;
                    border-radius: 0 4px 4px 4px;

                    > li > a {
                        margin: 0 5px;
                        padding: 5px 15px;
                        color: #555;

                        &:hover, &.active-trail {
                            color: #555;
                            background-color: #bfbfbf;
                            border-radius: 4px;
                        }
                    }
                }
            }
        }
    }

    @include breakpoint(0px 979px){
        #navbar-collapse {
            border: 0px;
            margin: 0px;
            padding: 0;
            width: auto;
            right: 0px;
            position: absolute;
            top: 49px;
    
            .navbar-nav {
                text-align: right;
                margin: 0;
                padding: 0.5em;
    
                > li {
                    border-radius: 4px;
                    a {
                        border-radius: 4px;
    
                        > &:hover, > &.active-trail {
                            background-color: #e7e7e7;
                            color: #555;
                        }
                    }
                }
    
                li.dropdown.open {
                    background-color: #e7e7e7;
                        
                    > a {
                        color: #555;
                    }
                }
    
                .dropdown-menu {
                    background-color: #e7e7e7;
                    text-align: right;
                    max-width: 100%;
    
                    > li {
                        display: flex;
                        justify-content: flex-end;

                        > a {
                            display: inline-block;
                            margin: 2px 5px;
                            border-radius: 4px;
                            @include fluid-type(320px, 1250px, 12px, 16px); // values inside are ($min_width,$max_width, $min_font, $max_font);
        
                            &:hover, &.active-trail {
                                color: #555;
                                background-color: #bfbfbf;
                            }
                        }
                    }
                }
            }
        }
    }
}

