/********************* MAIN MENU *********************/

#block-superfish-1 {
    position: static;
    margin-top: 1em;
    text-align: center;
    transition-property: background, padding;
    transition-duration: 300ms;
    transition-timing-function: cubic-bezier(0.22, 0.61, 0.36, 1);

    @include breakpoint(718px) {
        display: table;
        margin: 0 auto;
        margin-top: 1em;
    }

    &.sticky-menu {
        position: fixed;
        top: 0;
        z-index: 500;
        margin-top: 0;
        background: white;
        padding-top: 0.5em;
        padding-bottom: 0.5em;
        box-shadow: 0 0px 10px #333;

        @include breakpoint (718px) {
            padding-bottom: 0;
        }

        .block-content {
            display: inline-block;
            width: 100%;
            padding: 0 15px;
            vertical-align: middle;

            @include breakpoint (718px) {
                width: auto;
                padding: 0 0 0.5em 0;
            }
        }
    }

    .block-content,
    &.sticky-menu .block-content {
        display: inline-block;
        width: 100%;

        @include breakpoint (718px) {
            width: auto;
        }
    }

    #superfish-1 {
        display: table;
        margin: auto;

        >li {
            &:first-child {
                border-radius: 5px 0px 0px 5px;
            }

            &:last-child {
                border-radius: 0px 5px 5px 0px;
            }
        }

        li {
            border-bottom: 0;
            float: left;
            background: #eee url(images/bleeds.png) 50% 0px no-repeat;
            padding: 0;

            &:hover {
                background: #f8f8f8;
            }

            a {
                background-image: none;
                color: #333;
                text-shadow: #fff 0px 1px 0px;
                min-width: 70px;
                text-align: center;
                padding: 5px 10px;
                font-weight: bold;
                line-height: 20px;

                >.sf-sub-indicator {
                    position: static;
                    display: inline-block;
                    margin-left: 0.5em;
                }
            }
        }

        .sf-sub-indicator {
            filter: invert(70%);
        }

        .menuparent ul {
            width: auto !important;
            overflow: visible !important;

            li {
                white-space: nowrap !important;
                background: #ebebeb;

                &:hover {
                    background: white;
                }

                a {
                    text-align: left;
                }
            }
        }

        .active-trail {
            background-color: #7bc !important;

            >a {
                color: white;
                text-shadow: #38a 0px 1px 0px;
            }
        }
    }

    #superfish-1-toggle {
        display: block;
        text-align: center;
        background-color: #008db0;
        color: white;
        border-radius: 10px;
        line-height: 1.5;
        padding: 0.5em 1em;
        font-size: 18px;
        font-weight: bold;

        &.sf-expanded {
            background-color: #7bc;
            border-radius: 10px 10px 0 0;
        }

        &:hover {
            background-color: #7bc;
        }
    }

    #superfish-1-accordion {
        li {
            background-color: #e9eaec;
            line-height: 1.5;
            font-size: 16px;

            &.active-trail {
                background-color: #00728e;

                >a {
                    color: white;
                }
            }

            &:last-child {
                border-radius: 0 0 10px 10px;
            }

            &:hover {
                background-color: #9ec5d0;

                >a {
                    color: white;
                }
            }

            a {
                color: #00728e;
            }
        }
    }
}

