/********************* FORUMS *********************/
.path-forum {
    .region-content {
        max-width: 100%;
        overflow-x: auto;    
    }

    table {
        thead th,
        thead th a {
            text-align: left;
            padding: 5px 15px;
            background: #4e7232;
            color: white;
            white-space: nowrap;

            &:first-child {
                border-radius: 5px 0 0 5px;
                width: 64%;
            }
            &:last-child {
                border-radius: 0 5px 5px 0;
            }
            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(4) {
                text-align: center;
                width: 12%;
            }
        }
        tbody {
            tr {
                background: #fff;
                border-bottom: 1px solid #eaeaea;
    
                &:last-child {
                    border-bottom: none;
                }

                td:nth-child(2),
                td:nth-child(3),
                td:nth-child(4) {
                    text-align: center;
                }
            }
            td {
                border: medium none;
                padding: 15px;
                vertical-align: text-top;
            }
        }
    }

    .indent {
        margin-left: 1em;
    }
}

.page-node-type-forum {
    .author {
        text-align: center;
    }
    .field--name-taxonomy-forums {
        margin-bottom: 1em;
        text-align: center;

        .field--label {
            float: none;
            display: inline-block;
        }
        .field--item {
            display: inline-block;
        }
    }
}

