.bef-exposed-form .form--inline {
    column-count: 1;
    @include breakpoint(530px) {
        column-count: 2;
    }

    > .form-item {
        float: none;
        margin-bottom: 0.5em;
        margin-right: 0.5em;
        display: inline-block;
        width: 100%;
        max-width: 100%;
        margin-bottom: 0.5em;

        &.form-item-sort-by,
        &.form-item-sort-order {
            display: flex;
            align-items: center;
            justify-content: center;

            label {
                flex: 0 1 70px;
            }
            .select-wrapper {
                flex: 1 1 calc(100% - 70px);
            }
        }

        .label {
            display: block;
        }
    }

    .form-item-title input {
        width: 100%;
    }
    .form-item-field-publication-date-value,
    .form-item-sort-by,
    .form-item-sort-order {
        text-align: center;
    }

    .form-actions {
        width: 100%;
        text-align: center;
    }

    .select-wrapper,
    .select-wrapper select {
        width: 100%;
        max-width: 100%;
    }
}

.menu--footer {
